import { TOKEN_NAME } from "../constants";
import Storage from "../utils/Storage";
const SERVER_URL = process.env.NODE_ENV === "production"
    ? process.env.SERVER_URL_PROD
    : process.env.SERVER_URL_DEV;
function getAuthToken() {
    return Storage.get(TOKEN_NAME);
}
export function getStatusRequest() {
    return fetch(`${SERVER_URL}/api/status`);
}
export function getEarliestReceiptRequest() {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/get-earliest-receipt`, {
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
export function getReceiptsInDateRangeRequest(startDate, endDate) {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/get-receipts-in-date-range?startDate=${startDate}&endDate=${endDate}`, {
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
export function downloadReceiptsRequest() {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/download-receipts`, {
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
export function fetchReceiptImageRequest(id) {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/receipt-image/${id}`, {
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
export function deleteReceiptRequest(id) {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/delete-receipt/${id}`, {
        method: "DELETE",
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
export function updateReceiptRequest(id, updatedData) {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/update-receipt/${id}`, {
        method: "PUT",
        body: JSON.stringify(updatedData),
        headers: {
            "Authorization": `Bearer ${token}`,
            "Content-Type": "application/json"
        }
    });
}
export function getReceiptDescriptionsRequest() {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/get-descriptions`, {
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
// User/Password login
export function checkAuthRequest() {
    const token = getAuthToken();
    return fetch(`${SERVER_URL}/api/check-auth`, {
        // credentials: "include",
        headers: {
            "Authorization": `Bearer ${token}`
        }
    });
}
export function loginRequest({ username, password }) {
    return fetch(`${SERVER_URL}/api/login`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        // credentials: "include",
        body: JSON.stringify({ username, password })
    });
}
export function logoutRequest() {
    // return fetch(`${SERVER_URL}/api/logout`, {
    //     method: "POST"
    //     // credentials: "include"
    // });
}
// Google login
export function getGoogleUserInfoRequest() {
    return fetch(`${SERVER_URL}/api/me`, {
        method: "GET",
        credentials: "include",
        headers: {
            "Content-Type": "application/json"
        }
    });
}
export function googleLogoutRequest() {
    return fetch(`${SERVER_URL}/auth/google/logout`, {
        credentials: "include"
    });
}
