import React, { createContext, useContext, useState, useCallback, useEffect } from "react";
import { useParams } from "react-router-dom";
import { format, startOfMonth } from "date-fns";
import { history } from "./utils";
import { SortT } from "../../types";
const initSearchParams = new URLSearchParams(history.location.search);
const initialFilters = {
    startDate: initSearchParams.get("startDate"),
    endDate: initSearchParams.get("endDate"),
    sortBy: initSearchParams.get("sortBy"),
    query: initSearchParams.get("query")
};
const FiltersContext = createContext({
    ...initialFilters,
    onChangeFilters: () => { }
});
function isDateParams(key) {
    return key === "startDate" || key === "endDate";
}
const FiltersContextProvider = ({ children }) => {
    const pathParams = useParams();
    const [filters, setFilters] = useState(initialFilters);
    const onChangeFilters = useCallback((params) => {
        const searchParams = new URLSearchParams(history.location.search);
        const paramsKeys = Object.keys(params);
        paramsKeys.forEach((key) => {
            let value = params[key];
            if (value) {
                if (isDateParams(key)) {
                    value = format(value, "yyyy-MM-dd");
                }
                // @ts-ignore
                searchParams.set(key, value);
            }
            else {
                searchParams.delete(key);
            }
        });
        history.replace({
            pathname: history.location.pathname,
            search: searchParams.toString()
        });
        setFilters({
            ...filters,
            ...params,
            startDate: searchParams.get("startDate"),
            endDate: searchParams.get("endDate")
        });
    }, [filters]);
    useEffect(() => {
        const params = {};
        if (!filters.startDate) {
            params.startDate = startOfMonth(new Date());
        }
        if (!filters.endDate) {
            params.endDate = new Date();
        }
        if (!filters.sortBy) {
            params.sortBy = SortT.DateDesc;
        }
        if (Object.keys(params).length > 0 && !pathParams.id) {
            onChangeFilters(params);
        }
    }, [filters, pathParams?.id]);
    const value = {
        startDate: filters.startDate,
        endDate: filters.endDate,
        sortBy: filters.sortBy,
        query: filters.query,
        onChangeFilters
    };
    return (React.createElement(FiltersContext.Provider, { value: value }, children));
};
const useFilters = () => useContext(FiltersContext);
export { FiltersContextProvider, useFilters };
