import React, { createContext, useContext, useEffect, useState, } from "react";
import { useNavigate } from "react-router-dom";
import { checkAuthRequest, loginRequest } from "../../api";
import { TOKEN_NAME } from "../../constants";
import Storage from "../../utils/Storage";
const AuthContext = createContext({
    isLoading: true,
    isAuthenticated: false,
    googleUserInfo: null,
    login: async () => { },
    logout: () => { },
    setGoogleUserInfo: () => { }
});
const AuthContextProvider = ({ children }) => {
    const [isLoading, setIsLoading] = useState(true);
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const [googleUserInfo, setGoogleUserInfo] = useState(null);
    const navigate = useNavigate();
    useEffect(() => {
        async function checkLogin() {
            try {
                setIsLoading(true);
                // const googleUserInfoRes = await getGoogleUserInfoRequest();
                // const userInfo = await googleUserInfoRes.json();
                // if (userInfo && userInfo.user) {
                //     setGoogleUserInfo(userInfo.user);
                //     setIsLoading(false);
                //     return;
                // }
                const checkAuthDataRes = await checkAuthRequest();
                const checkAuthData = await checkAuthDataRes.json();
                setIsAuthenticated(checkAuthData.isAuthenticated);
                setIsLoading(false);
            }
            catch (error) {
                console.error("Auth check failed:", error);
                setIsAuthenticated(false);
                setIsLoading(false);
            }
        }
        checkLogin();
    }, []);
    const login = async (username, password) => {
        try {
            setIsLoading(true);
            const response = await loginRequest({ username, password });
            const data = await response.json();
            if (response.ok) {
                if (data.token) {
                    Storage.set(TOKEN_NAME, data.token);
                    setIsAuthenticated(true);
                    setIsLoading(false);
                    navigate("/", { replace: true });
                }
            }
            else {
                setIsLoading(false);
                throw new Error(data.message || "Login failed");
            }
        }
        catch (error) {
            console.error("Login error:", error);
            setIsAuthenticated(false);
            setIsLoading(false);
        }
    };
    const logout = () => {
        try {
            Storage.remove(TOKEN_NAME);
            navigate("/login", { replace: true });
        }
        catch (error) {
            console.error("Logout error:", error);
        }
        finally {
            setIsAuthenticated(false);
        }
    };
    const value = {
        isLoading,
        isAuthenticated,
        googleUserInfo,
        login,
        logout,
        setGoogleUserInfo
    };
    return (React.createElement(AuthContext.Provider, { value: value }, children));
};
const useAuth = () => useContext(AuthContext);
export { AuthContextProvider, useAuth };
