import { SortT } from "../types";
export function sortByDateTime(list, sortType) {
    return list.slice().sort((a, b) => {
        const dateA = new Date(a.date).getTime();
        const dateB = new Date(b.date).getTime();
        if (sortType === SortT.DateDesc) {
            return dateB - dateA;
        }
        return dateA - dateB;
    });
}
export function sortByPrice(list, sortType) {
    return list.slice().sort((a, b) => {
        const priceA = a.totalPrice;
        const priceB = b.totalPrice;
        if (sortType === SortT.PriceDesc) {
            return priceB - priceA;
        }
        return priceA - priceB;
    });
}
