const Storage = {
    set: (key, data) => {
        try {
            const jsonData = JSON.stringify(data);
            localStorage.setItem(key, jsonData);
        }
        catch (e) {
            console.warn("Something went wrong with saving storage item");
        }
    },
    get: (key) => {
        try {
            const data = localStorage.getItem(key);
            return data ? JSON.parse(data) : null;
        }
        catch (e) {
            console.warn("Something went wrong with reading storage item");
        }
    },
    remove: (key) => {
        try {
            localStorage.removeItem(key);
        }
        catch (error) {
            console.warn("Something went wrong with removing storage item");
        }
    }
};
export default Storage;
