import React, { useCallback, useMemo } from "react";
import Select from "react-select";
function UpdateDropdown({ propName, options, updatedData, setUpdatedData }) {
    const value = useMemo(() => {
        return options.find((option) => option.value === updatedData[propName]);
    }, [options, updatedData]);
    const onChange = useCallback((option) => {
        if (option && option.value) {
            setUpdatedData((prevData) => {
                if (!prevData) {
                    return prevData;
                }
                return {
                    ...prevData,
                    [propName]: option.value
                };
            });
        }
    }, [propName, setUpdatedData]);
    return (React.createElement(Select, { value: value, placeholder: "Select payment method", options: options, onChange: onChange }));
}
export default UpdateDropdown;
