    flex: 1;
    margin: 0;
    padding: 0 30px;
    overflow-y: scroll;
}

.styles__table___I9VYS {
    width: 100%;
    border-collapse: collapse;

    & th,
    & td {
        padding: 10px 6px;
        box-sizing: border-box;
        font-size: 14px;
        text-align: left;

        & button {
            display: flex;
            gap: 4px;
            padding: 2px 0;
            margin: 0;
            font-size: inherit;
            font-weight: bold;
            background-color: transparent;
            border: none;
        }
    }
    & tbody tr:hover {
        background-color: #efefef;
    }

    & .styles__date___ZN405 {
        min-width: 70px;
    }

    & .styles__time___PHmWO {
        min-width: 62px;
    }

    & .styles__shop___BpHvz {
        min-width: 150px;
    }

    & .styles__address___boDVk {
        min-width: 120px;
    }

    & .styles__paymentMethod___NZ6SJ {
        min-width: 78px;
    }

    & .styles__totalSumm___sfOIa {
        width: 90px;
        min-width: 90px;
        max-width: 90px;
    }
    & .styles__link___AiiBl {
        width: 84px;
        min-width: 84px;
        max-width: 84px;
    }

    & .styles__link___AiiBl {
        text-align: center;

        & .styles__navigateToButton___MgrjM {
            font-weight: normal;
            padding: 10px;
            background-color: #007bff;
            color: #ffffff;
            cursor: pointer;
            border-radius: 4px;
            transition: background-color 0.2s;

            &:hover {
                background-color: #0056b3;
            }
        }
    }
}

.styles__tooltip___CtJJW {
    position: relative;
    cursor: default;
    text-decoration: underline;
    text-decoration-style: dotted;
    text-decoration-color: red;
    transition: background-color 0.3s;
}
.styles__tooltipText___KxKmh {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s;
    position: absolute;
    bottom: 120%;
    left: 50%;
    text-align: center;
    line-height: 1.4;
    transform: translateX(-50%);
    background-color: #333;
    color: white;
    padding: 6px 8px;
    border-radius: 4px;
    white-space: nowrap;
    z-index: 10;
    font-size: 0.85rem;
    pointer-events: none;
}
.styles__tooltip___CtJJW:hover .styles__tooltipText___KxKmh,
.styles__tooltip___CtJJW:focus .styles__tooltipText___KxKmh {
    visibility: visible;
    opacity: 1;
}

.styles__footerContainer___kPqc4 {
    overflow-y: scroll;
    display: flex;
    justify-content: flex-end;
    padding: 0 114px 0 30px;
}

.styles__footTotalSumm___pFwE2 {
    width: 92px;
    padding: 10px 6px;
    font-size: 14px;
    box-sizing: border-box;
}
