import React, { useCallback } from "react";
import styles from "./styles.css";
const SERVER_URL = process.env.NODE_ENV === "production"
    ? process.env.SERVER_URL_PROD
    : process.env.SERVER_URL_DEV;
function GoogleLoginButton() {
    const handleLogin = useCallback(() => {
        const { pathname, search } = window.location;
        window.location.href = `${SERVER_URL}/auth/google?state=${encodeURIComponent(`${pathname}${search}`)}`;
    }, []);
    return (React.createElement("button", { onClick: handleLogin, className: styles.googleButton },
        React.createElement("img", { src: "https://www.svgrepo.com/show/475656/google-color.svg", alt: "Google logo", className: styles.googleIcon }),
        "Sign in with Google"));
}
export default GoogleLoginButton;
