import React, { createContext, useContext, useState, useEffect, useCallback } from "react";
import getInitLanguage from "../../utils/getInitLanguage";
import loadLocalesData from "../../utils/loadLocalesData";
const initialLocales = {
    language: getInitLanguage(),
    messages: {},
    isLoading: true,
    onSelectLanguage: () => () => { }
};
const LocalesContext = createContext(initialLocales);
const LocalesContextProvider = ({ locales, children }) => {
    const [language, setLanguage] = useState(initialLocales.language);
    const [messages, setMessages] = useState(initialLocales.messages);
    const [isLoading, setIsLoading] = useState(initialLocales.isLoading);
    const onSelectLanguage = useCallback((language) => () => {
        setLanguage(language);
    }, []);
    useEffect(() => {
        async function loadData() {
            try {
                const [loadedMessages] = await Promise.all(
                // @ts-ignore
                loadLocalesData(language, locales));
                if (loadedMessages && loadedMessages.default && loadedMessages.default.MainTitle) {
                    document.title = loadedMessages.default.MainTitle;
                }
                setMessages(loadedMessages.default);
                setIsLoading(false);
            }
            catch (error) {
                console.error(error);
            }
        }
        loadData();
    }, [language]);
    const value = {
        language,
        messages,
        isLoading,
        onSelectLanguage
    };
    return (React.createElement(LocalesContext.Provider, { value: value }, children));
};
const useLocales = () => useContext(LocalesContext);
export { LocalesContextProvider, useLocales };
