import React, { createContext, useContext, useState, useEffect } from "react";
import { getEarliestReceiptRequest, getReceiptDescriptionsRequest } from "../../api";
import { getQuerySearchRe } from "../../constants/regexes";
import { useFilters } from "../Filters";
import loadChecks from "../../utils/loadChecks";
import { getDescriptionKey } from "../../utils/getDescriptionKey";
const initialData = {
    receipts: [],
    earliestReceipt: null,
    latestReceipt: null,
    descriptions: {},
    isLoading: true,
    setReceipts: () => { },
    setDescriptions: () => { }
};
const DataContext = createContext(initialData);
const DataContextProvider = ({ children }) => {
    const [receipts, setReceipts] = useState(initialData.receipts);
    const [earliestReceipt, setEarliestReceipt] = useState(initialData.earliestReceipt);
    const [latestReceipt, setLatestReceipt] = useState(initialData.latestReceipt);
    const [descriptions, setDescriptions] = useState(initialData.descriptions);
    const [isLoading, setIsLoading] = useState(initialData.isLoading);
    const { startDate, endDate, query } = useFilters();
    useEffect(() => {
        async function loadData() {
            try {
                const [earliestReceiptResponse, receiptDescriptionsResponse] = await Promise.all([
                    getEarliestReceiptRequest(),
                    getReceiptDescriptionsRequest()
                ]);
                const earliestReceipt = await earliestReceiptResponse.json();
                const receiptDescriptions = await receiptDescriptionsResponse.json();
                setEarliestReceipt(earliestReceipt.earliest);
                setLatestReceipt(earliestReceipt.latest);
                setDescriptions(receiptDescriptions.descriptions);
            }
            catch (error) {
                console.error(error);
            }
        }
        loadData();
    }, []);
    useEffect(() => {
        async function loadData() {
            try {
                const [loadedReceipts] = await Promise.all([
                    loadChecks({ startDate, endDate })
                ]);
                setReceipts(loadedReceipts);
                setIsLoading(false);
            }
            catch (error) {
                console.error(error);
            }
        }
        loadData();
    }, [startDate, endDate]);
    let sortedReceipts = receipts;
    if (query) {
        const queryRe = getQuerySearchRe(query);
        console.log(query, queryRe);
        sortedReceipts = receipts.filter((receipt) => (receipt.items.some((item) => {
            const descriptionKey = getDescriptionKey(item.name);
            return queryRe.test(item.name) || queryRe.test(descriptions[descriptionKey] || "");
        })));
    }
    const value = {
        receipts: sortedReceipts,
        earliestReceipt,
        latestReceipt,
        descriptions,
        isLoading,
        setReceipts,
        setDescriptions
    };
    return (React.createElement(DataContext.Provider, { value: value }, children));
};
const useData = () => useContext(DataContext);
export { DataContextProvider, useData };
